/* class hasn't changed much since part 1. All it does now is resize the canvas
a bit differently and draw rectangles to the buffer. */

function drawRectangle(
  x: number,
  y: number,
  width: number,
  height: number,
  color: string,
  canvas: HTMLCanvasElement
) {
  const context = canvas.getContext("2d");
  context.fillStyle = color;
  context.fillRect(Math.floor(x), Math.floor(y), width, height);
}
function fill(color: string, canvas: HTMLCanvasElement) {
  const context = canvas.getContext("2d");

  context.fillStyle = color;
  context.fillRect(0, 0, context.canvas.width, context.canvas.height);
}

function renderCanvas(
  canvas: HTMLCanvasElement,
  bufferCanvas: HTMLCanvasElement
) {
  console.log(canvas);
  console.log(bufferCanvas);
  const context = canvas.getContext("2d");
  const bufferContext = bufferCanvas.getContext("2d");
  context.drawImage(
    bufferContext.canvas,
    0,
    0,
    bufferContext.canvas.width,
    bufferContext.canvas.height,
    0,
    0,
    context.canvas.width,
    context.canvas.height
  );
}
function test() {
  console.log("yoyo");
}
function resize(ratioSize: number, currentSize: number, ratio: number): number {
  return ratioSize > ratio ? currentSize * ratio : currentSize / ratio;
}
export const Display = (canvas: HTMLCanvasElement) => {
  const bufferCanvas = document.createElement("canvas");
  return {
    renderCanvas: renderCanvas.bind(this, canvas, bufferCanvas)
  };
};
